<template>
    <div>
        <div class="border-bottom">
            <v-col cols="auto">
                <div class="d-flex align-center justify-end">
                    <v-btn class="primaryBtn mr-2" elevation="0" style="width: auto;" @click="(addUpdateRoleModal = true) && (modalType = 'add')">
                        <img src="@/assets/icons/add.svg"
                         alt="export" class="mr-1" width="23">
                        <span class="fs-16px">Qo'shish</span>
                    </v-btn>
                </div>
            </v-col>
        </div>
        <v-data-table
            :headers="headers"
            :items="items"
            item-key="id"
            :loading="loading"
            disable-sort
            hide-default-footer
            class="w-100"
        >
            <!-- eslint-disable  -->
            
            <template v-slot:item.name="{item}">
                <div class="d-flex justify-center">
                    <span class="text-black">{{ item.name }}</span>
                </div>
            </template>
            <template v-slot:item.actions="{item}">
                <div class="d-flex align-center justify-center">
                    <div class="cursor-pointer mr-2 mt-1" v-if="$can('update', 'warehouse_ip_kirim')">
                        <img src="@/assets/icons/edit.svg" alt="edit" class="cursor-pointer"
                        @click="editRole(item)">
                    </div>
                    <!-- <div class="cursor-pointer mt-1" @click="deleteRole(item)" v-if="$can('delete', 'warehouse_ip_kirim')">
                        <img src="@/assets/icons/trush-square.svg" alt="edit-2">
                    </div> -->
                </div>
            </template>

            <template v-slot:no-data>
                <div>
                    <no-data class="mt-6"></no-data>
                    <br>
                    <h2 class="ml-6 mt-1">Ma'lumotlar topilmadi!</h2>
                </div>
            </template>

        </v-data-table>
        
        <!-- <v-pagination
            v-model="page"
            :length="pageCount"
            total-visible="7"
            class="my-2"
        ></v-pagination> -->


        <v-dialog
            max-width="400"
            persistent
            v-model="addUpdateRoleModal"
        >
            <v-card class="aksessuar_add">

                <div class="aksessuar_add__title">
                    <span class="text-black">Rol ma`lumotlari</span>
                    <v-btn icon @click="closeModal()">
                        <img src="@/assets/images/BackButton.svg" alt="BackButton">
                    </v-btn>
                </div>

                <ValidationObserver ref="formAddUpdateRole" v-slot="{ handleSubmit, invalid }">
                    <form class="aksessuar_add__content" @submit.prevent="handleSubmit(onSubmit)">
                      
                        <div class="mt-4">
                            <span class="text-black ml-1">Nomi</span>
                            <ValidationProvider :rules="{required: true,
                             error: [errorStatus, errorMessage, errorPlace, 'name']}"
                              v-slot="{ errors }">
                                <v-text-field outlined v-model="role.name" class="mt-2"
                                    placeholder="Misol: Manager" :error-messages="errors">
                                </v-text-field>
                            </ValidationProvider>
                        </div>
                       
                       
                        <v-card-actions class="d-flex justify-space-between">
                            <v-btn
                                text
                                class="backBtn"
                                elevation="0"
                                @click="closeModal()"
                            >Bekor qilish
                            </v-btn>
                            <v-btn
                                class="primaryBtn"
                                elevation="0"
                                :loading="loading"
                                :disabled="invalid"
                                type="submit"
                                @click="loader = 'loading'"
                            >{{ modalType == 'add' ? 'Qo\'shish' : 'Tahrirlash'}}
                            </v-btn>
                        </v-card-actions>

                    </form>
                </ValidationObserver>
            </v-card>
        </v-dialog>


        <!-- <DeleteModal :deleteModal="deleteModal" :user_id="one_user.id"
         :actionName="'main/deleteRole'" :modalTitle="'Foydalanuvchi o\'chirilishi'"
         :modalDesc="'Bu foydalanuvchini o‘chirmoqchimisiz?'"></DeleteModal> -->
    </div>
</template>

<script>
import {mapState, mapActions} from "vuex";
import { loaderMixin } from '@/mixins/dateLoaderMixins.js'
import NoData from "@/components/base/NoData.vue";
// import DeleteModal from '@/components/modals/DeleteModal.vue'

export default {
    props: ['update'],
    mixins: [loaderMixin],
    components:{
        // DeleteModal,
        NoData
    },
    data(){
        return{
            // ============= SECONDARY_DATA =============
            // page: 1,
            // pageCount: 0,
            // deleteModal: false,
            loading: false,
            addUpdateRoleModal: false,
            modalType: 'add',
            
            role: {
                name: '',
            },
            headers: [
                {text: 'Nomi', value: 'name', align: 'center', class: 'table-header'},
                {text: 'Harakatlar', value: 'actions', align: 'center', class: 'table-header'},
            ],
            items: [],
            // ============= SECONDARY_DATA =============



            // ============= PRIMARY_DATA =============
            errorStatus: false,
            errorMessage: '',
            errorPlace: ''
            // ============= PRIMARY_DATA =============
        }
    },
    watch: {
        // 'page': async function (newVal, oldVal) {
        //     if (newVal) {
        //         await this.refresh()
        //     }
        // },
        'role': {
            handler: async function(newValue) {
                if(newValue){
                    this.clearErrors();
                }
            },
            deep: true,
        },
        '$store.state.main.users_list': {
            handler: async function(value) {
                if(value){
                    await this.refresh()
                    this.$store.commit('main/SET_UPDATE_USERS_LIST', false)
                }
            },
            deep: true,
            immediate: true
        },
    },
    async mounted(){
        await this.refresh();
    },
    computed:{
        ...mapState(
            {
                roles: state => state.main.roles,
                searchValue: state => state.main.searchValue,
                dateFilterValue: state => state.main.dateFilterValue,
            }
        ),
    },
    methods:{
        ...mapActions(
            {
                // fetchUsers: 'main/fetchUsers',
                fetchRoles: 'main/fetchRoles',
                postRoles: 'main/postRoles',
                updateRoles: 'main/updateRoles',
                // deleteRole: 'main/deleteRole',
                // fetchOneUser: 'main/fetchOneUser'
            }
        ),
        async editRole(item){
            this.addUpdateRoleModal = true;
            this.modalType = 'edit';

            this.role = item
            // await this.fetchOneUser(item.id)
            // this.user.name = this.one_user.name
            // this.user.position = this.one_user.role_id
            // this.user.email = this.one_user.email
        },
        // async deleteRole(item){
        //     this.deleteModal = true;
        //     this.role = item
        // },



        // ============= CLEARS_AND_CLOSES START =============
        clearInputs(){
            this.role = {
                name: '',
            },
            this.clearErrors();
        },
        clearErrors(){
            this.errorStatus = false;
            this.errorMessage = '';
            this.errorPlace = '';
        },
        closeModal(){
            this.clearInputs();
            this.$refs.formAddUpdateRole.reset();
            this.addUpdateRoleModal = false;
        },
        // ============= CLEARS_AND_CLOSES END =============



        async refresh() {
            this.loading = true

            await this.fetchRoles();
            this.items = this.roles
            this.items.splice(0,1);

            this.loading = false
        },


        // ============= ON_SUBMIT START =============
        async onSubmit(){
            this.$refs.formAddUpdateRole.validate().then(async(success) => {
                if(success){
                    // ============= IF SUCCESS =============

                    if(this.modalType == 'add'){
                       
                        // ============= ADDING_NEW_ROLE START =============
                        let a = await this.postRoles({
                            name: this.role.name,
                        })
                        
                        if (a.response && a.response.status == 422) {
                            if(a.response.data.errors.name){
                                this.errorMessage = a.response.data.errors.name[0];
                                this.errorPlace = 'name'
                            }
                            this.errorStatus = true;
                            this.$toast.error(this.errorMessage);
                        }else if(a.response && a.response.status == 409){
                            if(a.response.data.message){
                                this.errorMessage = a.response.data.message;
                            }
                            this.$toast.error(this.errorMessage);
                        }else{
                            await this.refresh()
                            this.$toast.success('Muvaffaqiyatli qo`shildi!');
                            this.closeModal();
                        }
                        // ============= ADDING_NEW_ROLE END =============
                    }else if(this.modalType == 'edit'){
                        // ============= UPDATING_ROLE START =============
                        let a = await this.updateRoles({
                            id: this.role.id,
                            name: this.role.name,
                        })
    
                        if (a.response && a.response.status == 422) {
                            if(a.response.data.errors.name){
                                this.errorMessage = a.response.data.errors.name[0];
                                this.errorPlace = 'name'
                            }
                            this.errorStatus = true;
                            this.$toast.error(this.errorMessage);
                        }else if(a.response && a.response.status == 409){
                            if(a.response.data.message){
                                this.errorMessage = a.response.data.message;
                            }
                            this.$toast.error(this.errorMessage);
                        }else{
                            await this.refresh()
    
                            this.$toast.success('Muvaffaqiyatli qo`shildi!');
                            this.closeModal();
                        }
                        // ============= UPDATING_ROLE END =============
                    }
                }else{
                    // ============= IF NO SUCCESS =============
                    this.$toast.error('Xato yuz berdi!');
                }
            })

        },
        // ============= ON_SUBMIT END =============
    }
}
</script>

<style>

</style>
